/* Desktop and tablet */
:root {
  --section-translateY: calc(var(--section-margin) * 0.6);
  --navbar-translateY: var(--navbar-margin); }
  @media (max-width: 767px) {
    :root {
      --main-margin: 40px;
      --section-margin: 40px;
      --navbar-margin: 20px;
      --navbar-strech: calc(-1 * 15px); } }
  @media (min-width: 768px) and (max-width: 991px) {
    :root {
      /* !important: overwrite desktop's value */
      --container-width: 720px !important; } }
  @media (min-width: 768px) {
    :root {
      --main-margin: 70px;
      --navbar-margin: 25px;
      --section-margin: 70px;
      --navbar-strech: calc(-1 * ((100vw - var(--container-width)) / 2 + 15px));
      --container-width: 900px; } }

html {
  height: 100%; }

body {
  background-color: #212529;
  color: #adb5bd;
  font-family: "Lato", "arial", sans-serif;
  font-weight: 400;
  overflow-x: hidden;
  letter-spacing: 0.0075em; }
  @media (max-width: 767px) {
    body {
      margin: var(--main-margin) 1rem; } }
  @media (min-width: 768px) {
    body {
      font-size: 1.2rem; } }
  body a {
    color: #63e6be; }
  body h1,
  body h2,
  body h3,
  body h4,
  body h5,
  body h6 {
    margin-top: calc(1rem * 1.5);
    margin-bottom: 1rem;
    font-weight: 400; }
  body h2 {
    font-weight: 600; }

.header {
  margin-top: var(--navbar-margin) 0px; }
  .header .logo {
    line-height: normal; }
    @media (max-width: 767px) {
      .header .logo {
        margin-bottom: var(--navbar-margin);
        text-align: center;
        display: block; } }
    @media (min-width: 768px) {
      .header .logo {
        margin-left: -5px; } }
    .header .logo a {
      font-family: "Jura", "arial", sans-serif;
      color: #adb5bd;
      font-size: 60px;
      letter-spacing: 2px;
      text-transform: uppercase;
      padding: 0px; }
      .header .logo a:hover {
        text-decoration: none; }
  .header .social-link {
    color: #adb5bd;
    text-align: center;
    text-transform: uppercase;
    margin: 0px 20px; }
    .header .social-link i {
      font-size: 25px;
      margin: 5px 0px; }

nav.navbar {
  background-color: #212529;
  margin: var(--navbar-margin) var(--navbar-strech);
  padding-right: calc(-1 * var(--navbar-strech));
  padding-left: calc(-1 * var(--navbar-strech));
  z-index: 1000; }
  @media (max-width: 767px) {
    nav.navbar {
      transition: transform 1250ms, opacity 1250ms;
      position: sticky;
      top: 0; } }
  @media (min-width: 768px) {
    nav.navbar {
      position: fixed;
      bottom: 0;
      margin-left: calc(var(--navbar-strech) - 1rem);
      margin-bottom: 0;
      padding-top: 1rem;
      padding-bottom: 1rem;
      transition: transform 1250ms, opacity 1250ms, margin-left 250ms ease; } }
  nav.navbar.before-animation {
    opacity: 0;
    transform: translateY(var(--navbar-translateY)); }
  nav.navbar.sticky {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12); }
    @media (min-width: 768px) {
      nav.navbar.sticky {
        margin-left: var(--navbar-strech);
        box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12); } }
  @media (max-width: 767px) {
    nav.navbar ul.nav.nav-pills {
      flex-wrap: nowrap;
      white-space: nowrap;
      overflow-x: auto;
      width: 100%;
      justify-content: space-between; } }
  @media (max-width: 767px) {
    nav.navbar ul.nav.nav-pills li.nav-item {
      margin: 0px 5px; } }
  @media (min-width: 768px) {
    nav.navbar ul.nav.nav-pills li.nav-item {
      margin: 0px 10px; } }
  nav.navbar ul.nav.nav-pills li.nav-item:first-child {
    margin-left: 0px !important; }
  nav.navbar ul.nav.nav-pills li.nav-item:last-child {
    margin-right: 0px !important; }
  nav.navbar ul.nav.nav-pills li.nav-item a.nav-link {
    border-radius: 0.5rem; }
    @media (max-width: 767px) {
      nav.navbar ul.nav.nav-pills li.nav-item a.nav-link {
        padding-left: 0.5rem;
        padding-right: 0.5rem; } }
    nav.navbar ul.nav.nav-pills li.nav-item a.nav-link.active {
      color: #212529;
      background-color: #63e6be;
      font-weight: 600; }

main {
  margin: var(--main-margin) 0px; }
  main h1 {
    font-weight: 300; }
    @media (max-width: 767px) {
      main h1 {
        margin-top: 0px; } }
    @media (min-width: 768px) {
      main h1 {
        font-size: 3.4rem; } }
  main h2 span.github-button {
    vertical-align: middle;
    margin-left: 0.5rem; }
  main h2 small.programme {
    display: block;
    margin-top: 10px;
    font-size: 1.75rem; }
  main h2 small.university {
    display: block;
    margin-top: 10px;
    font-size: 1.2rem; }
  main .section {
    margin-top: var(--section-margin);
    margin-bottom: var(--section-margin);
    transition: transform 1250ms, opacity 1250ms; }
    @media (min-width: 768px) {
      main .section {
        height: calc(100vh - (1.2rem*1.5 + 0.5rem*2 + 1rem*2));
        display: flex;
        overflow-y: scroll;
        flex-direction: column;
        flex-wrap: wrap; } }
    main .section.before-animation {
      opacity: 0;
      transform: translateY(var(--section-translateY)); }
    main .section a {
      text-decoration: none;
      color: #adb5bd;
      background-image: linear-gradient(#63e6be, #63e6be);
      background-repeat: no-repeat;
      background-size: 100% 10%;
      background-position: 0 95%;
      transition: 0.35s ease; }
      main .section a:hover {
        border-radius: 2px;
        color: #495057;
        background-size: 100% 100%; }
    main .section > *:first-child {
      margin-top: 0px; }
    main .section > *:last-child {
      margin-bottom: 0px; }
    main .section p {
      line-height: 1.7; }
    main .section li {
      margin-bottom: 1rem; }
    main .section .table-container div.table-header {
      display: flex;
      flex-wrap: nowrap;
      width: 100%;
      border-bottom: 1px solid #adb5bd; }
      main .section .table-container div.table-header div.term {
        font-weight: 600;
        flex-grow: 2; }
      main .section .table-container div.table-header div {
        padding: .75rem; }
    main .section .table-container table.table tbody th,
    main .section .table-container table.table tbody td {
      border: 0px; }
      main .section .table-container table.table tbody th:first-child,
      main .section .table-container table.table tbody td:first-child {
        width: 100%; }
      main .section .table-container table.table tbody th:last-child,
      main .section .table-container table.table tbody td:last-child {
        text-align: center; }
    main .section p.tags-heading {
      margin-bottom: 0.5rem;
      width: 100%; }
    main .section .tags-container {
      display: flex;
      flex-wrap: wrap;
      margin: 0px -0.3rem; }
      main .section .tags-container .tag {
        display: inline-flex;
        list-style: none;
        margin: 0.3rem 0.25rem;
        padding: 0.25rem 0.75rem;
        background-color: #343a40;
        border-radius: 100px;
        font-size: 1rem; }
    main .section .section-item {
      display: block;
      width: 70%;
      margin-right: 5%; }
      main .section .section-item:last-child {
        margin-right: 0; }

footer small {
  display: block; }

.overlay {
  visibility: hidden;
  opacity: 0;
  z-index: 2000;
  background-color: rgba(0, 0, 0, 0.9);
  transition: 250ms ease-out;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: hidden; }
  .overlay .overlay-container {
    overflow-y: auto;
    height: 100%; }
    .overlay .overlay-container a#close-btn {
      position: sticky;
      float: right; }
      @media (max-width: 767px) {
        .overlay .overlay-container a#close-btn {
          top: 0.5rem;
          right: 0.5rem;
          padding: 0.5rem; } }
      @media (min-width: 768px) {
        .overlay .overlay-container a#close-btn {
          top: 1rem;
          right: 1rem;
          padding: 0.5rem; } }
      .overlay .overlay-container a#close-btn svg {
        fill: #adb5bd; }
        @media (max-width: 767px) {
          .overlay .overlay-container a#close-btn svg {
            width: 1.5rem;
            height: 1.5rem; } }
        @media (min-width: 768px) {
          .overlay .overlay-container a#close-btn svg {
            width: 1.75rem;
            height: 1.75rem; } }
    .overlay .overlay-container #resources-list h2 {
      margin-top: 3rem; }
    .overlay .overlay-container #resources-list h3 {
      margin-top: 1.5rem; }
    .overlay .overlay-container #resources-list li {
      word-break: break-word;
      margin-bottom: 1rem; }
